.container {
  justify-content: center;
  margin: 4rem;
  display: flex;
}

.grid {
  grid-template-rows: repeat(5, 1fr);
  grid-template-columns: repeat(5, 1fr);
  gap: 16px;
  width: 60%;
  display: grid;
}

.cultura {
  background-image: url("torii-black.1a252f7e.jpg");
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  border-radius: 30px;
  flex-direction: column;
  grid-area: span 5 / span 1;
  justify-content: start;
  align-items: center;
  width: 100%;
  height: 75vh;
  transition: all 2s;
  display: flex;
}

.cultura:hover, .sabores:hover, .localizacion:hover, .enlaces:hover {
  cursor: pointer;
  transition: all 2s;
  transform: scale(1.1);
}

.cultura-contenido, .localizacion-contenido {
  color: #fff;
  text-align: center;
  text-shadow: 2px 2px 4px #000000b3;
  max-width: 80%;
  margin-top: 6rem;
  font-size: 1.5rem;
  font-weight: bold;
}

.sabores {
  background-image: url("ramen.133803db.jpg");
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  border-radius: 30px;
  flex-direction: column;
  grid-area: span 5 / span 3;
  justify-content: start;
  align-items: center;
  width: 100%;
  height: 75vh;
  transition: all 2s;
  display: flex;
}

.sabores-contenido {
  color: #fff;
  text-align: center;
  text-shadow: 2px 2px 4px #000000b3;
  max-width: 80%;
  margin-top: 2rem;
  font-size: 1.5rem;
  font-weight: bold;
}

.localizacion {
  background-image: url("street.07f3c8ee.jpg");
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  border-radius: 30px;
  flex-direction: column;
  grid-area: span 5 / span 1;
  justify-content: start;
  align-items: center;
  width: 100%;
  height: 75vh;
  transition: all 2s;
  display: flex;
}

.enlaces-contenido {
  color: #000;
  text-shadow: 2px 2px 4px #000000b3;
  font-size: 1.5rem;
  font-weight: bold;
}

.enlaces {
  background-color: #000;
  background-image: url("japan-footer.a2bcfac4.png");
  background-position: center;
  background-repeat: repeat;
  background-size: contain;
  border-radius: 15px;
  grid-area: span 5 / span 5;
  justify-content: center;
  align-items: flex-start;
  height: 3rem;
  transition: all 2s;
  display: flex;
}

a {
  text-decoration: none;
  display: contents;
}

@media (width <= 1080px) {
  .container {
    justify-content: center;
    height: 1rem;
    margin-top: 2rem;
    display: flex;
  }

  .grid {
    grid-template-columns: 1fr;
    grid-auto-rows: auto;
    gap: 16px;
    width: 100%;
    display: grid;
    height: auto !important;
  }

  .cultura {
    grid-area: 1 / 1;
    height: auto;
    min-height: 20rem;
  }

  .sabores {
    grid-area: 2 / 1;
    height: 20rem;
  }

  .localizacion {
    grid-area: 3 / 1;
    height: auto;
    min-height: 20rem;
  }

  .enlaces {
    background-repeat: repeat-x;
    grid-area: 4 / 1;
    height: 3rem;
    margin-bottom: 0;
    padding-bottom: 0;
  }
}

.header {
  background-color: #000;
  border-bottom-right-radius: 3rem;
  border-bottom-left-radius: 3rem;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  margin: 0;
  padding: 1rem 1.5rem;
  display: flex;
}

.logo a img {
  max-width: 3.5rem;
}

.logo {
  justify-content: center;
  align-items: center;
  display: flex;
}

ul {
  list-style: none;
}

.nav-list {
  justify-content: space-between;
  display: flex;
}

.nav-list li {
  margin: .5rem;
}

.nav-list li:nth-child(-n+4):after {
  content: "|";
  color: #fff;
  font-size: 11px;
  position: relative;
  left: .5rem;
}

.link-header {
  color: #fff;
  font-size: 11px;
  font-weight: 700;
  text-decoration: none;
  transition: all 1s;
}

.link-header:hover, .link-footer:hover {
  color: red;
  transition: all 1s;
}

@media (width <= 1080px) {
  .header {
    flex-direction: row;
    justify-content: space-between;
    padding: 1.5rem 3rem 2rem;
  }

  .nav-list {
    flex-wrap: wrap;
    justify-content: space-between;
    display: flex;
  }

  @media (width <= 600px) {
    .link-header {
      font-size: 9px;
    }

    .nav-list li:nth-child(-n+4):after {
      content: "|";
      color: #fff;
      font-size: 9px;
      position: relative;
      left: .5rem;
    }

    .header {
      flex-direction: column;
      justify-content: center;
      display: flex;
    }
  }
}

.footer {
  color: #fff;
  background-color: #000;
  border-top-left-radius: 3rem;
  border-top-right-radius: 3rem;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 100%;
  margin-top: auto;
  padding: 0 0 20px;
  font-size: 9px;
  font-weight: 400;
  line-height: 20px;
  display: flex;
  position: relative;
  bottom: 0;
}

.img-footer {
  max-width: 5rem;
}

.socials {
  margin-top: 0;
  display: flex;
}

.politicas {
  flex-direction: column;
  justify-content: center;
  align-items: center;
  line-height: 15px;
  display: flex;
}

.img-socials {
  object-fit: contain;
  width: auto;
  height: 12px;
}

.img-socials:hover {
  opacity: .7;
}

.link-footer {
  color: #fff;
  text-decoration: none;
}

@media (width >= 1080px) {
  .footer {
    flex-wrap: wrap;
    grid-template-rows: 50% 50%;
    grid-template-columns: 50% 50%;
    padding: 1.5rem 3rem 2rem;
    display: grid;
  }

  .img-footer {
    margin: 10px 0;
  }

  #p-footer {
    grid-area: 2 / 1 / 2 / 2;
  }

  .socials {
    justify-content: flex-end;
  }

  .politicas {
    flex-direction: row;
    justify-content: flex-end;
    gap: 22px;
    display: flex;
  }

  #pol-priv:after, #cookies:after {
    content: "|";
    font-size: 11px;
    position: relative;
    left: 1em;
  }

  .img-socials {
    height: 19px;
  }
}

.categoria-main {
  width: 80%;
  margin: 3rem auto;
}

.categoria-grid {
  grid-template-rows: 1fr 1fr 1fr;
  grid-template-columns: 1fr 1fr 1fr;
  gap: 2rem 0;
  margin-top: 2rem;
  display: grid;
}

.categoria-card {
  border: 1px solid #000;
  border-radius: 85px 85px 1rem 1rem;
  width: 85%;
  padding-bottom: 1rem;
  transition: all 1s;
  overflow: hidden;
}

.categoria-card:hover {
  transition: all 1s;
  transform: scale(1.1);
  box-shadow: .5rem .5rem 1rem #000;
}

.categoria-item-pic {
  position: relative;
}

.categoria-item-img {
  width: 100%;
  display: block;
}

.categoria-item-content {
  background-color: #fff;
  margin: 0;
  padding: 15px;
}

.categoria-link-blog-h3 {
  color: #000;
  font-size: 1.2em;
  text-decoration: none;
}

.categoria-link-blog-h3:hover {
