  line-height: 20px;
}

.categoria-link-btn {
  color: #000;
  background-color: #0000;
  border: 1px solid #000;
  padding: 7px;
  font-size: 14px;
  text-decoration: none;
  display: inline-block;
}

.categoria-link-btn:hover {
  color: #daa520;
  border-color: #daa520;
}

@media (width <= 1080px) {
  @media (width <= 600px) {
    .categoria-grid {
      grid-template-columns: 1fr;
      place-items: center;
      gap: 2rem;
      margin-top: 2rem;
      display: grid;
    }
  }
}

.detalle-titulo {
